<template>
  <input type="text" v-model="obj[attr]" @focus="textFocus" @blur="textBlur" />
</template>

<script>
export default {
  name: 'TextInput',
  props: ['obj', 'attr', 'defualt'],
  mounted () {
    this.obj[this.attr] = this.defualt;
  },
  methods: {
    textFocus (e) {
      if (this.obj[this.attr] == this.defualt) {
        this.obj[this.attr] = '';
      }
    },
    textBlur (e) {
      if (this.obj[this.attr] == '') {
        this.obj[this.attr] = this.defualt;
      }
    }
  }
}
</script>

<style>
</style>


