<template>
  <input type="text" v-model="obj[attr]" @focus="pwdFocus" @blur="pwdBlur" />
</template>

<script>
export default {
  name: 'PasswordInput',
  props: ['obj', 'attr', 'default', 'events'],
  mounted () {
    this.obj[this.attr] = this.default;
    if (this.events != undefined) {
      for (let e of this.events) {
        this.$el.addEventListener(e.type, e.fun);
      }
    }
  },
  methods: {
    pwdFocus (e) {
      if (this.obj[this.attr] == this.default) {
        e.target.type = 'password';
        this.obj[this.attr] = '';
      }
    },
    pwdBlur (e) {
      if (this.obj[this.attr] == '') {
        e.target.type = 'text';
        this.obj[this.attr] = this.default;
      }
    },
  }
}
</script>

<style>
</style>


