<template>
  <div class="registerBox">
    <div class="title">注册</div>
    <text-input :obj="user" :attr="'sno'" :defualt="'学号'"></text-input>
    <text-input :obj="user" :attr="'username'" :defualt="'用户名'"></text-input>
    <password-input :obj="user" :attr="'pwd'" :default="'密码'"></password-input>
    <password-input :obj="user" :attr="'rpwd'" :default="'确认密码'"></password-input>
    <button @click="submit">确定</button>
    <a @click="$router.push('/login')">已有账号，直接登录</a>
  </div>
</template>

<script>
import PasswordInput from './PasswordInput.vue';
import TextInput from './TextInput.vue';
import { register } from 'network/Login.js';

export default {
  name: 'RegisterBox',
  data () {
    return {
      user: {
        sno: '',
        username: '',
        pwd: '',
        rpwd: '',
      }
    }
  },
  components: {
    TextInput,
    PasswordInput,
  },
  methods: {
    submit () {
      let sno = this.user.sno;
      let pwd = this.user.pwd;
      let username = this.user.username;
      let rpwd = this.user.rpwd;
      if (pwd != rpwd) {
        alert('两次输入的密码不一致 !');
        return;
      }
      register({
        sno,
        pwd,
        username
      }).then(res => {
        if (res.data.status) {
          this.$store.commit('setSno', sno);
          tool.setCookie({
            sno
          }, 1);
          this.$router.push('/home');
        } else {
          alert(res.data.message);
        }
      }).catch(err => {
        console.log(err);
      });
    }
  }
}

</script>

<style>
.registerBox {
  margin: 160px auto;
  width: 360px;
  text-align: center;
}
</style>


